const Synthpresets = [
  {
    volume: 20,
    harmonicity: 1,
    modulationIndex: 5,
    detune: 5,
    oscillator: {
      type: `saw`
    },
    envelope: {
      attack: 0.1,
      decay: 0.1,
      sustain: 0.1,
      release: 0.1
    },
  }
];

export default Synthpresets;



